const THEME_TRON = {
  player_color: '#0ac757',
  enemy1_color: '#9e18aa',
  tubular_wall1_color: '#f737f7',
  tubular_wall2_color: '#35e0df',
}
const THEME_KENNEY = {}
// const CURRENT_LEVEL = window.currentLevel ? LEVELS[window.currentLevel] : LEVELS[0]

const [player1, player2] = getPlayers(2)
let playersOnArena = [];
let playerRef;
const useLevels = levelId => {
  const CURRENT_LEVEL = LEVELS[levelId]
  const enemyRefs = CURRENT_LEVEL.enemies.map(() => useRef(null))
  const wallRefs = CURRENT_LEVEL.arena.walls.map(() => useRef())
  playerRef = useRef(null)
  const pos = useRef(CURRENT_LEVEL.enemies.length + 1)
  useEvents(CHARACTER_EVENTS.OUT_OF_RING, characterRef => {
    const isUserFall = characterRef === playerRef
    const playersAlive = playersOnArena.filter(pRef => pRef !== characterRef)
    const thereArePlayersLeft = playersAlive.length > 0
    const onlyOnePlayersLeft = playersAlive.length === 1
    const userIsAlive = playersAlive.find(pRef => pRef === playerRef)
    if (!isUserFall && userIsAlive) {
      pos.current = pos.current - 1
      window.ReactNativeWebView?.postMessage(`${ENEMY_DOWN}:${pos.current}`);
    }
    if (thereArePlayersLeft && isUserFall) {
      setTimeout(() => {
        console.log('game over', GAME_OVER, pos.current)
        window.ReactNativeWebView?.postMessage(`${GAME_OVER}:${pos.current}`);
      }
      , 1000)
    }
    if (userIsAlive && onlyOnePlayersLeft) {
      setTimeout(() => {
        console.log('game win', GAME_WON)
        window.ReactNativeWebView?.postMessage(GAME_WON);
      }
      , 1000)
    }
    playersOnArena = playersAlive;
  })
  playersOnArena.push(...[playerRef, ...enemyRefs])
  return [
    <Player
      position={CURRENT_LEVEL.player.position}
      radius={0.5}
      userController={player2}
      color={CURRENT_LEVEL.player.color}
      ref={playerRef}
    />,
    CURRENT_LEVEL.enemies.map((enemy, idx) => 
      <Enemy
        key={enemy.id}
        position={enemy.position}
        radius={0.5}
        color={enemy.color}
        ref={enemyRefs[idx]}
        enemies={[playerRef]}
        model={enemy.model}
        spin={enemy.spin}
      />
    ),
    CURRENT_LEVEL.arena.walls.map((wall, idx) =>
      <Wall
        key={`wall_${idx}`}
        position={wall.position}
        color={wall.color}
        rotate={wall.rotate}
        hx={0.25}
        hy={3}
        ref={wallRefs[idx]}
      />
    ),
    CURRENT_LEVEL.crystals.map((crystal, idx) =>
      <Crystal
        key={`crystal_${idx}`}
        position={crystal.position}
        scale={[1.5, 1.5, 1.5]}
        type={crystal.type}
        timeToAppear={crystal.timeToAppear}
      />
    ),
    CURRENT_LEVEL.portalGroups?.map((portals, idx) =>
      <Portals
        key={`portals_${idx}`}
        positionBlue={portals[1].position}
        positionRed={portals[0].position}
      />
    ),
    CURRENT_LEVEL.spinReverseAreas?.map((area, idx) =>
      <RevertSpinArea
        key={`spin_area_${idx}`}
        position={area.position}
        hx={area.hx}
        hy={area.hy}
        rotate={area.rotate}
      />
    )
  ]
}

const GameScene = ({ onLoad, currentLevel = 0 }) => {
  console.log(currentLevel)
  const [player, enemies, walls, crystals, portals, spinReverseAreas] = useLevels(currentLevel)
  const arena = LEVELS[currentLevel].arena

  useEvents(CHARACTER_EVENTS.BEGIN_CONTACT, contact => {
    const fixtureA = contact.GetFixtureA()
    const fixtureB = contact.GetFixtureB()
    const dataA = fixtureA.GetBody().GetUserData()
    const dataB = fixtureB.GetBody().GetUserData()
    if ((dataA.isPlayer || dataA.isEnemy || dataA.isWall) && (dataB.isPlayer || dataB.isEnemy || dataB.isWall)) {
      window.ReactNativeWebView?.postMessage(SIMPLE_HIT);
    }
  })
  useEvents(GAME_EVENTS.SYSTEM_ONLINE, () => {
    window.ReactNativeWebView?.postMessage(SCENE_LOADED);
  })
  useEvents(CHARACTER_EVENTS.GETS_FREEZER, () => {
    window.ReactNativeWebView?.postMessage(FROZEN);
  })
  useEvents(CHARACTER_EVENTS.GETS_COIL, () => {
    window.ReactNativeWebView?.postMessage(COILED);
  })
  useEvents(CHARACTER_EVENTS.OVERCHARGE, () => {
    window.ReactNativeWebView?.postMessage(OVERCHARGED);
  })
  useEvents(CHARACTER_EVENTS.PICK_CRYSTAL_GROW, () => {
    window.ReactNativeWebView?.postMessage(GROWN);
  })
  useEvents(CHARACTER_EVENTS.TELEPORT, () => {
    window.ReactNativeWebView?.postMessage(TELEPORTED);
  })
  useEvents(CHARACTER_EVENTS.IMPULSE_ON, ({uuid, api}) => {
    const data = api.getPhysicsUserData(uuid)
    window.ReactNativeWebView?.postMessage(`${IMPULSE_ON}:${data.model}`);
  })
  useEvents(CHARACTER_EVENTS.IMPULSE_OFF, ({uuid, api}) => {
    const data = api.getPhysicsUserData(uuid)
    window.ReactNativeWebView?.postMessage(`${IMPULSE_OFF}:${data.model}`);
  })
  
  
  
  return (
    <>
      <WorldEvents>
        <Canvas
          style={{ background: "#171717" }}
          shadowMap
          // invalidateFrameloop
          // pixelRatio={Dimensions.get('window').height / Dimensions.get('window').width}
          camera={{ position: [-5, 11, 20], fov: 30 }}
          concurrent
          gl={{ antialias: true }}
          onCreated={({ gl, camera }) => {
            camera.lookAt(0, 0, 3)
            // onLoad()
          }}
        >
          {/* <PosIndicator /> */}
          <CountDown />
          {/* <pointLight color="0xffffff" intensity={1} distance={10} position={[0, 50, 0]} />
          <pointLight color="0xffffff" intensity={0.2} distance={10} position={[-5, 50, 0]} /> */}
          <ambientLight intensity={1} color="#dadada"/>
        
          <pointLight position={[-5, 50, 0]} intensity={0.2} distance={10} color="lightblue" />
          
          <directionalLight
            castShadow
            position={[5, 8, -5]}
            intensity={0.5}
            // color="#ff9760"
            color="#ffffff"
            shadow-mapSize-width={600}
            shadow-mapSize-height={600}
            shadow-camera-far={50}
            shadow-camera-left={-10}
            shadow-camera-right={10}
            shadow-camera-top={10}
            shadow-camera-bottom={-10}
            />
          <Physics>
            {crystals}
            {enemies}
            {player}
            {walls}
            {portals}
            {spinReverseAreas}
          </Physics>
          <Suspense fallback={null}>
            <Rock
              texture={arena.texture}
              tint={arena.tint}
              name={arena.name}
              scale={[16, 16, 16]}
              position={[0, -8, 0]}
              receiveShadow
            />
          </Suspense>
          {/* <UfoBase position={[0, 0.15, 0]} /> */}
          {/* <Arena/> */}
          {/* <Arena3 /> */}
          {/* <EffectComposer>
          </EffectComposer> */}
            {/* <Bloom luminanceThreshold={0.01} luminanceSmoothing={1} height={100} /> */}
            {/* <Outline selection={[wallRef1, wallRef2, wallRef3, wallRef4]} visibleEdgeColor="#ffffff" edgeStrength={1} pulseSpeed={1} blur={true} /> */}
          {/* <Sparks count={50} mouse={sparkRef} colors={['#182825', '#016FB9', '#22AED1', '#6D8EA0', '#224870', '#224870']} /> */}
          
          {/* <OrbitControls /> */}
          {/* <axesHelper /> */}
          {/* <gridHelper args={[30, 30, 30]} /> */}
          <Stars/>
        </Canvas>
      </WorldEvents>
      <Controllers forPlayers={[player1, player2]} noUI/>
    </>
  );
};

export default GameScene;
