const CameraHelper = ({ pivot }) => {
  const rotationSpeed = useRef()
  const elevationFactor = useRef(0.002)
  const animeTimeline = useRef()
  
  useFrame(() => {
    if (pivot.current) {
      if (rotationSpeed.current) {
        if (pivot.current.children[0].position.z > 2) {
          pivot.current.children[0].position.z -= 0.002
        }

        // if still going up
        if (elevationFactor.current > 0) {
          // normal rotation
          pivot.current.rotation.y += rotationSpeed.current
        } else {
          if (elevationFactor.current < 0) {
            rotationSpeed.current -= 0.0000105
            if (rotationSpeed.current > 0) {
              pivot.current.rotation.y += rotationSpeed.current
            } else {
              window.ReactNativeWebView?.postMessage(THE_END_ENDS)
            }
          }
        }
          
        if (pivot.current.children[0].position.y < 2.5) {
          pivot.current.children[0].position.y += elevationFactor.current
        } else {
          // slow down elevation
          elevationFactor.current -= 0.00001
          // while positive (or it will go dow)
          if (elevationFactor.current > 0) {
            pivot.current.children[0].position.y += elevationFactor.current
          }
          // console.log(pivot.current.rotation.y)
        }
      } else {
        rotationSpeed.current = 0.0075
        // setStartAnimation(true)
      }

      pivot.current.children[0].lookAt(0, pivot.current.children[0].position.y, 0)
    }
  })
  return null
}
