import React, { useEffect, useState, forwardRef, useRef } from 'react';
import { TextureLoader, FileLoader, THREE } from 'expo-three'
import { Text } from 'drei'
import { Asset } from 'expo-asset';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader';
import { OBJLoader } from 'three/examples/jsm/loaders/OBJLoader';
import { MTLLoader } from 'three/examples/jsm/loaders/MTLLoader'
import { useThree } from 'react-three-fiber';
import cubeObj from '../../../assets/cube.obj'
import cubeMtl from '../../../assets/cube.mtl'

const loaderObj = new OBJLoader();
const loaderMtl = new MTLLoader();
const loaderTxt = new TextureLoader();

const normalMapImage = loaderTxt.load(require('../../../assets/textures/cube_rock_A_mat_normal.png'))

export default forwardRef((props, ref) => {
  const fwdRef = ref || useRef()
  const [geometry, setGeometry] = useState()
  const { texture, tint, name = '', ...moreProps } = props
  const mapImage = loaderTxt.load(texture)
  
  const loadObject = async () => {
    
    const mtl = loaderMtl.parse(cubeMtl)
    mtl.preload();
    // mtl.materials.cube_rock_A_mat.map = mapImage
    // mtl.materials.cube_rock_A_mat.normalMap = normalMapImage
    loaderObj.setMaterials(mtl)
    const obj = loaderObj.parse(cubeObj)
    // replace MeshStandardMaterial by MeshLambertMaterial sice the last one
    // is more performant and includes shadows.
    const rockMaterial = new THREE.MeshLambertMaterial({
      color: tint,
      transparent: false,
      opacity: 1,
      wireframe: false,
      side: THREE.FrontSide,
      map: mapImage,
      normalMap: normalMapImage,
    })
    // obj.children[0].material.map = mapImage
    // obj.children[0].material.normalMap = normalMapImage
    obj.children[0].material = rockMaterial
    obj.children[0].receiveShadow = true
    obj.children[0].castShadow = true
    obj.children[0].side = THREE.FrontSide
    // console.log(obj)
    
    setGeometry(obj)
    // scene.add(obj)
  }
  
  useEffect(loadObject,[])
  // console.log(geometry ? 'LOADED' : 'NOT YET')
  return (
    <>
      <group
        position={[0, -1.9, 7.9]}
      >
        <Text
          font={require('../../../assets/fonts/Sandoval.ttf')}
          fontSize={1}
          anchorX="center" // default
          anchorY="middle" // default
          material={new THREE.MeshBasicMaterial({color: tint, opacity: 0.5, transparent: true })}
        >
          {name.toUpperCase()}
        </Text>
      </group>
      <group
        ref={fwdRef}
        {...moreProps}
        dispose={null}
      >
        {geometry &&
          <primitive object={geometry} />
        }
      </group>
    </>
  )
})