import React, { useEffect, useRef, useState } from 'react'
import GameScene from './GameScene'
import TitleScene from './TitleScene'
import DebugScene from './DebugScene'
import TheEndScene from './TheEndScene'
import {
  ON_LINE_NOTIFICATION,
  SCENE_LOADED,
} from '../constants/webView_comm_messages'

const sceneSelector = sceneName => {
  switch (sceneName) {
    case 'TitleScene':
      return TitleScene
    case 'GamePlayScene':
      return GameScene
    case 'DebugScene':
      return DebugScene
    case 'TheEndScene':
      return TheEndScene
    default:
  }
}

export default function scenes() {
  const [sceneName, setScene] = useState(window.SS_initial_scene)
  const sceneParams = useRef({})
  const SceneToRender = sceneSelector(sceneName)
  const notifyOnLoad = useRef(() => {
    console.log('NOTIFY ALL LOADED')
    // notify scene loaded
    window.ReactNativeWebView?.postMessage(SCENE_LOADED)
  }).current
  
  useEffect(() => {
    // methods to change scenes from global scope
    window.SS_change_scene = (sceneName, params = {}) => {
      sceneParams.current = params
      setScene(sceneName)
    }
    // notify system on-line
    window.ReactNativeWebView?.postMessage(ON_LINE_NOTIFICATION)
  }, [])

  return SceneToRender ? <SceneToRender onLoad={notifyOnLoad} {...sceneParams.current}/> : null
}
