import React, {useRef} from 'react'
import { useState } from 'react'
import { Image, StyleSheet, View, PanResponder, Text } from 'react-native'
import { useFonts } from 'expo-font'
const base_skin_stone = require('../../../assets/ui/star_base_stone.png')
const base_skin_graphite = require('../../../assets/ui/star_base_graphite.png')
const skin_foreground_stone = require('../../../assets/ui/star_filled_stone.png')
const skin_foreground_graphite = require('../../../assets/ui/star_filled_graphite.png')

export default function RectangularButton({ empty = true, variant = 'stone', style }) {
  const skin = empty
    ? (
      variant === 'stone'
        ? base_skin_stone
        : base_skin_graphite
      )
    : (
      variant === 'stone'
        ? skin_foreground_stone
        : skin_foreground_graphite
      )
  return (
    <View style={[styles.button, style]}>
      <Image source={skin} style={styles.image} />
    </View>
  )
}

const styles = StyleSheet.create({
  button: {
    flex: 1,
    alignItems: 'center',
    justifyContent: 'center',
    width: '100%',
    height: '100%',
    maxWidth: 476/2,
    maxHeight: 504/2,
    // borderWidth: 1,
    // borderColor: 'blue',
  },
  baseImage: {},
  image: {
    zIndex: -1,
    position: 'absolute',
    width: '100%',
    height: '100%',
    // borderWidth: 1,
    // borderColor: 'red',
    resizeMode: 'contain',
    justifyContent: 'center',
  }
});
