import React, { useState, useEffect, useRef } from 'react'
import { useThree } from 'react-three-fiber'
import anime from '../../helpers/anime';

const CameraAnimation = () => {
  const { camera } = useThree()
  useEffect(() => {
    // const destination = {...camera.position}
    // camera.position.x = -3
    // camera.position.y = -15
    // camera.position.z = 10
    // camera.lookAt(0, 0, 3)

    // 0, 2, 4

    const targetObj = { ...camera.position }

    const timeline = anime.timeline({
      easing: Math.random() > 0.5 ? 'easeInOutExpo' : 'easeInOutBack',
      duration: 6000 * 3,
      targets: camera.position,
      direction: 'alternate',
      loop: true,
      update: function () {
        camera.lookAt(0, 0, 0)
      },
      complete: function () {
        console.log('complete')
      }
    })
    timeline
      .add({
        x: 1.5,
        y: 2.5,
        z: 4.3,
      })
      .add({
        x: -1,
        y: 3,
        z: 4.5,
      })
    
  }, [])
  return null
}

export default CameraAnimation
