import React, { useRef, forwardRef, useState, useEffect } from 'react'
import { useLoader } from 'react-three-fiber'
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader'
import { EffectComposer, Outline, Bloom, SelectiveBloom } from '@react-three/postprocessing'
import { THREE } from 'expo-three'

import { OBJLoader } from 'three/examples/jsm/loaders/OBJLoader';
import { MTLLoader } from 'three/examples/jsm/loaders/MTLLoader'

import modelObj from '../../../assets/trophy/individual_trophy.obj'
import modelMtl from '../../../assets/trophy/individual_trophy.mtl'

const loaderObj = new OBJLoader();
const loaderMtl = new MTLLoader();

// const assetURL = 'https://raw.githubusercontent.com/Cosmitar/threejs_assets/master/ufo_ships/enemy_ufoPurple.glb'

const getGradientMap = color => {
  const alphaIndex = 1
  const colors = new Uint8Array( alphaIndex + 2 )

  for ( let c = 0; c <= colors.length; c ++ ) {

    colors[ c ] = ( c / colors.length ) * 256;

  }

  const gradientMap = new THREE.DataTexture( colors, colors.length, 1, THREE.LuminanceFormat );
  gradientMap.minFilter = THREE.NearestFilter;
  gradientMap.magFilter = THREE.NearestFilter;
  gradientMap.generateMipmaps = false;
  return gradientMap
}

const textureLoader = new THREE.TextureLoader()

export default forwardRef((props, ref) => {
  const fwdRef = ref || useRef()
  const [geometry, setGeometry] = useState()
  const { colorPalette = [], ...moreProps } = props

  const threeTone = textureLoader.load(require('../../../assets/threeTone.jpg'));
  threeTone.minFilter = THREE.NearestFilter;
  threeTone.magFilter = THREE.NearestFilter;
  
  const matTest = new THREE.MeshBasicMaterial({ color: 'lime' })
  const materialChassis = colorPalette[0] ? new THREE.MeshToonMaterial({ color: colorPalette[0], gradientMap: threeTone }) : new THREE.MeshLambertMaterial({ color: colorPalette[0] })
  const materialDeco = colorPalette[1] ? new THREE.MeshToonMaterial({ color: colorPalette[1], gradientMap: threeTone }) : new THREE.MeshLambertMaterial({ color: colorPalette[0] })
  const materialCore = colorPalette[2] ? new THREE.MeshToonMaterial({ color: colorPalette[2], gradientMap: threeTone }) : new THREE.MeshLambertMaterial({ color: colorPalette[0] })
  const materialDome = colorPalette[3] ? new THREE.MeshToonMaterial({ color: colorPalette[3], gradientMap: threeTone }) : new THREE.MeshLambertMaterial({ color: colorPalette[0] })

  const loadObject = () => {
    const mtl = loaderMtl.parse(modelMtl)
    mtl.preload();
    loaderObj.setMaterials(mtl)
    const obj = loaderObj.parse(modelObj)
    setGeometry(obj)
  }

  useEffect(loadObject,[])

  return !geometry ? null : (
    <EffectComposer>
      <Outline selection={[
        { current: geometry.children[1] }
      ]} visibleEdgeColor="golden" edgeStrength={1} pulseSpeed={0} blur={true} />
      <group
        ref={fwdRef}
        scale={[8,8,8]}
        {...moreProps}
      >
        {geometry &&
          <primitive object={geometry} />
        }
      </group>
    </EffectComposer>
  )
})