import React, { Suspense, useEffect, useRef, useState } from 'react'
import { useFrame } from 'react-three-fiber'
import Ufo from './../../components/UfoModels/UfoModel1'

export default () => {
  const [spin, setSpin] = useState()
  const ufoRef = useRef()
  useFrame(() => {
    if (ufoRef?.current && spin) {
      ufoRef.current.rotation.y += 0.03;
    }
  })

  useEffect(() => {
    setSpin(true)
  }, [])
  return (
    <Ufo
      ref={ufoRef}
      colorPalette={[
        // '#b4b4d8',
        // '#ea5627',
        // '#1f1f7b',
        // '#e4e4ef'

        '#999106',
        '#990606',
        '#000000',
        '#e4e4ef'
      ]}
      position={[0, 0, 0]}
    />
  )
}