import React from 'react'
import { StyleSheet } from 'react-native'
import { View } from 'react-native'
import Star from './star'

const Stars = ({ variant = "stone", value = 2, style = {} }) => {
  return  (
    <View style={[{
      flex: 1,
      flexDirection: 'row',
      // borderWidth: 1,
    }, style]}>
      <Star variant={variant} empty={value < 1} style={[styles.smallStar, styles.leftStar]} />
      <Star variant={variant} empty={value < 2} />
      <Star variant={variant} empty={value < 3} style={[styles.smallStar, styles.rightStar]} />
    </View>

  )
}

export default Stars

const styles = StyleSheet.create({
  smallStar: {
    // width: 476/2 * 0.9,
    // height: 504/2 * 0.9,
    marginTop: 10,
  },
  leftStar: {
    transform: [{rotate: '-10deg'}, { scale: 0.9 }],
  },
  rightStar: {
    transform: [{rotate: '10deg'}, { scale: 0.9 }],
  }
})
