import React, { useState, useEffect, useRef } from 'react'
import { Html, useAspect } from 'drei'
import { Dimensions } from 'react-native'
import { useThree } from 'react-three-fiber'
import { useEvents } from '../../systems/World/hooks'
import { GAME_EVENTS } from '../../systems/World/Events'
import anime from '../../helpers/anime';

const CountDown = () => {
  const [count, setCount] = useState()
  const [start, setStart] = useState()
  const gameReadyTrigger = useEvents(GAME_EVENTS.READY_ON)
  const systemOnlineTrigger = useEvents(GAME_EVENTS.SYSTEM_ONLINE)
  const { camera } = useThree()
  const tId = useRef()
  useEffect(() => {
    if (count === 0) {
      gameReadyTrigger()
    } else {
      if (count !== undefined) {
        tId.current = setTimeout(() => {
          setCount(count - 1)
        }, 1000)
      }
    }
    () => {
      clearTimeout(tId.current)
    }
  }, [count])
  useEffect(() => {
    if (!start) return
    const destination = {...camera.position}
    console.log(destination)
    camera.position.x = -3
    camera.position.y = -15
    camera.position.z = 10
    camera.lookAt(0, 0, 3)
    const targetObj = { ...camera.position }
    anime({
      easing: Math.random() > 0.5 ? 'easeInOutExpo' : 'easeInOutBack',
      duration: 6000,
      x: destination.x,
      y: destination.y,
      z: destination.z,
      targets: targetObj,
      update: function () {
        camera.position.x = targetObj.x
        camera.position.y = targetObj.y
        camera.position.z = targetObj.z
        camera.lookAt(0, 0, 3)
      },
      complete: function () {
        setCount(3)
      }
    })
  }, [start])
  useEffect(() => {
    setTimeout(() => {
      systemOnlineTrigger()
      setStart(true)
    }, 2000)
  }, [camera])
  const scale = useAspect(
    "cover",
    Dimensions.get("window").width,
    Dimensions.get("window").height,
    1
  )
  return (
    <Html scaleFactor={scale} position={[-scale.width, 0, -scale.height]}>
      { count !== 0 &&
        <div
          className="content"
          style={{
            // border: '1px solid red',
            color: 'white',
            fontFamily: 'Arial',
            fontWeight: 'bold',
            display: 'flex',
            flex: 1,
            width: '100vw',
            height: '100vh',
            boxSizing: 'border-box',
            flexDirection: 'column',
            alignItems: 'center',
            justifyContent: 'center',
          }}>
          <span style={{ fontSize: 180 }}>{count}</span>
        </div>
      }
    </Html>
  )
}
export default CountDown